<template>
  <div class="guidelines">
    <NavHeader :links=this.navLinks />
    <div class="content has-gap">
      <div class="loading" v-if="!guideline"></div>
    <div class="guideline" v-if="guideline">
      <div class="title"><h1>{{guideline.title}}</h1><img v-if="guideline.verified" src="@/assets/images/verified.png" class="verified_badge" title="Diese Guideline ist verifiziert"><router-link :to="'/editor?g=' + this.$route.query.g"><img src="@/assets/images/edit.svg" alt="Diese Guideline bearbeiten" v-if="this.$store.getters.isLoggedIn && this.guideline.author_id == this.$store.getters.getUser.id" id="editButton"></router-link></div>
      <h2>von <router-link :to="'/user?u=' + guideline.author_id">{{guideline.name}}</router-link></h2>
      <p v-html="guideline.text" id="text"></p>
      <div class="bibliography" v-if="guideline.bibliography && guideline.bibliography.length > 0">
        <h3>Quellen</h3>
        <p v-for="source in guideline.bibliography" :key="source.id" :value="source.id"><b>[{{source.id}}]</b> {{source.text}}</p>
      </div>
      <p id="last_update">letzte Aktualisierung: {{guideline.last_update}}</p>
      <div class="approvements">
        <h3 v-if="guideline.approvements && guideline.approvements.length > 0">durch <b>{{guideline.approvements.length}}</b> Experten bestätigt</h3>
        <h3 v-if="guideline.approvements && guideline.approvements.length < 1">noch nicht bestätigt</h3>
        <div class="approvers">
          <router-link v-for="approvement in guideline.approvements" :to="'/user?u=' + approvement.id" :key="approvement.userId" :value="approvement.userId"><img :src=approvement.image><p class="expert_name">{{approvement.name}}</p></router-link>
        </div>
        <button id="approve_button" @click="approveGuideline" v-if="this.$store.getters.isLoggedIn && this.$store.getters.getUser && this.$store.getters.getUser.is_expert && !this.alreadyApproved && this.guideline.author_id != this.$store.getters.getUser.id"><img src="@/assets/images/checkmark.png">Guideline Bestätigen</button>
        <button id="approve_button" class="revert" @click="revertApproval" v-if="this.$store.getters.isLoggedIn && this.$store.getters.getUser && this.$store.getters.getUser.is_expert && this.alreadyApproved"><img src="@/assets/images/cancel.svg" style="filter: invert(100%);">Bestätigung zurückziehen</button>
      </div>
      <div id="annotation">
        <div v-if="activeAnnotation">
          <img :src="activeAnnotation.image">
        </div>
        <div class="annotation_content" v-if="activeAnnotation">
          <h2>{{activeAnnotation.name}}</h2>
          <p>{{activeAnnotation.annotation_text}}</p>
        </div>
        <div class="annotation_voting">
          <div class="vote_button" id="upvote" @click="handleAnnotationVote"></div>
          <p v-if="activeAnnotation">{{activeAnnotation.score}}</p>
          <div class="vote_button" id="downvote" @click="handleAnnotationVote"></div>
        </div>
      </div>
      <div id="annotation_editor" :class="{active: selectedText}">
        <h2>Annotation Verfassen</h2>
        <q>{{selectedText}}</q>
        <textarea rows="10" v-model="annotationText"></textarea>
        <div style="display: flex; gap: 10px;">
          <button class="send_annotation_button cancel" @click="selectedText = null">Abbruch</button>
          <button class="send_annotation_button" @click="saveAnnotation">Senden</button>
        </div>
      </div>
    </div>
  </div>
  </div>
</template>

<script>

import NavHeader from "@/components/navHeader.vue";
import moment from "moment";
import hljs from "highlight.js";
import 'highlight.js/styles/a11y-dark.css';
import AuthService from "@/services/AuthService";
import { debounce } from 'debounce';

export default {
  name: 'HomeView',
  components: {NavHeader},
  data(){
    return {
      navLinks: [
        {
          link: "/guidelines",
          name: "guidelines"
        }
      ],
      selectedText: null,
      activeAnnotation: null,
      timeOfAnnotationOpen: null,
      annotationText: null,
      guideline: null,
      guidelineTEMPLATE: {
        title: "Optimieren des DOM für Screenreader",
        author: "Thomas Schmitz",
        author_id: 1,
        last_update: "06.07.2023",
        text: "HTML ist eine Auszeichnungssprache und muss von <a id='999' class='annotationLink'>jedem professionellen Webentwickler beherrscht werden</a>. Ein standardkonformes HTML-Grundgerüst erleichtert Software wie Browsern und Hilfsmitteln behinderter Nutzer die korrekte Aufbereitung einer Webseite. Bei der Barrierefreiheit geht es vor allem um ein strukturiertes und linearisierbares HTML-Grundgerüst. Erst wenn ein Dokument mit einer Auszeichnungssprache semantisch aufbereitet wird, lassen sich weitere Prinzipien von Webstandards, etwa die Trennung von Inhalt und Layout, effizient umsetzen. Diese Seite bietet einen knappen Überblick über verfügbare Layout-Techniken. HTML bietet drei grundlegende Layouttechniken.\n\nStandardkonformität setzt u.a. die Trennung von Inhalt und Layout voraus, d.h. alle Aspekte von Layout und Formatierung sind über CSS vorzunehmen; damit wird die Darstellung von Webseiten auf diverse Endgeräte erst möglich. Tabellen und Frames sind ungeeignet für eine barrierefreie Webgestaltung, weil sie die visuelle Anordnung am Bildschirm vorgeben. Bevor ein CSS-Design umgesetzt werden kann, muss die Linearisierbarkeit und Strukturierung der Inhalte im HTML vorgenommen werden. Eine weitere Voraussetzung für Barrierefreiheit ist die Berücksichtigung der Prinzipien des Progressive Enhancement, d.h. Inhalte müssen immer genutzt werden können, auch wenn ergänzende Techniken wie Bilder oder JavaScript im Browser des Nutzers nicht unterstützt werden oder abgeschaltet sind. Progressive Enhancement besagt, dass HTML stets die Grundtechnik für alle Informationen und Funktionen ist und alle weiteren Techniken, seien es CSS oder JavaScript, aber auch der Einsatz von Grafiken und anderen Objekten als optional zu betrachten sind. Die Entwicklung von Webstandards war vor allem in den 1990er rasant. Zwischenzeitlich hat sich die Verabschiedung von Webstandards stark verändert, weil veröffentlichte Webstandards auch den Praxistest bestehen müssen.\n\nDer Einsatz von gültigem HTML ist ein Teilaspekt von Barrierefreiheit. Vor allem ist der korrekte Syntax die Voraussetzung für valides HTML — ein Qualitätsmerkmal für Webentwickler. Mit dieser Tabelle können alle jemals spezifizierten HTML-Elemente in HTML 2.0, HTML 3.2, HTML 4.01, XHTML, HTML5 und HTML 5.1 nebeneinander verglichen werden. Einige Elemente gibt es nicht mehr, einige haben es nie wirklich geschafft und einige werden in der Zukunft eine neue Bedeutung bekommen. Für die Webentwicklung wird mit Accessible Rich Internet Applications (ARIA) zunächst ein Satz von Attributen bestimmt, die HTML, SVG und andere Auszeichnungssprachen mit mehr Semantik ausstatten. Diese betreffen u.a. Webanwendungen: Mit ARIA-Attributen können Widgets so ausgezeichnet werden, dass sie semantisch solchen Komponenten entsprechen, über die ein Betriebssystem verfügt, mit der Folge, dass sie von Screenreadern und anderen Hilfsmitteln besser bedient werden können. ARIA umfasst aber weitaus mehr als einige Attribute. Zunächst ist ARIA eine Spezifikation an Browser und Hilfsmittel. Darüber hinaus hat der Webstandard einen merklichen Einfluss auf HTML5 gehabt. Insgesamt geht es aber darum, dass Screenreader und andere Hilfsmittel sich auf eine spezielle Schnittstelle des Betriebssystems stützen und dass ARIA die Technik ist, die die Webentwicklung benötigt, um diese Schnittstelle sinnvoll zu bedienen.",
        verified: true,
        bibliography: [],
        approvements:[
          {
            userId: 101,
            name: "Theresa Sauer",
            image: "data:image/png;base64,"
          },
          {
            userId: 102,
            name: "Maike Schütze",
            image: "data:image/png;base64,"
          },
          {
            userId: 103,
            name: "Thomas Müller",
            image: "data:image/png;base64,"
          },
          {
            userId: 104,
            name: "Kirsten Meier",
            image: "data:image/png;base64,"
          },
          {
            userId: 105,
            name: "Dana Schmitz",
            image: "data:image/png;base64,"
          }
        ]
      },
      alreadyApproved : false
    }
  },
  created(){
    if (this.$route.query.g == 3){
      this.guideline = {
        title: "Guideline mit Code",
        name: "Jim Halpert",
        author_id: 2,
        last_update: "26.03.2023",
        text: "HTML ist eine Auszeichnungssprache und muss von jedem professionellen Webentwickler beherrscht werden. Ein standardkonformes HTML-Grundgerüst erleichtert Software wie Browsern und Hilfsmitteln behinderter Nutzer die korrekte Aufbereitung einer Webseite. <a id='999' class='annotationLink'>Bei der Barrierefreiheit geht es vor allem um ein strukturiertes und linearisierbares HTML-Grundgerüst.</a> Erst wenn ein Dokument mit einer Auszeichnungssprache semantisch aufbereitet wird, lassen sich weitere Prinzipien von Webstandards, etwa die Trennung von Inhalt und Layout, effizient umsetzen. Diese Seite bietet einen knappen Überblick über verfügbare Layout-Techniken. HTML bietet drei grundlegende Layouttechniken, und hier folgt etwas Code: \n\n<pre><p>HTML</p><code><xmp><!DOCTYPE html>\n" +
            "<html>\n" +
            "<body>\n" +
            "\n" +
            "<h1>This is heading 1</h1>\n" +
            "<h2>This is heading 2</h2>\n" +
            "<h3>This is heading 3</h3>\n" +
            "<h4>This is heading 4</h4>\n" +
            "<h5>This is heading 5</h5>\n" +
            "<h6>This is heading 6</h6>\n" +
            "\n" +
            "</body>\n" +
            "</html>\n" +
            "\n</xmp></code></pre>\n\nFür das Syntax Higlighting wird dabei highlight.js eingebunden. Theoretisch kann zwischen einer vielzahl an Farbschemata gewählt werden, doch hier wirkte es naheliegend, das 'a11y-dark' Thema zu wählen. Javascript oder jegliche andere Programmiersprachen sind selbstverständlich auch möglich, viel Spaß mit etwas Beispielcode aus der Vue-Komponente der aktuellen Seite:\n\n<pre><p>JS</p><code><xmp>for(let i in annoLinks){\n" +
            "      if (typeof(annoLinks[i]) === \"object\"){\n" +
            "        annoLinks[i].addEventListener(\"click\", this.getAnnotation);\n" +
            "      }\n" +
            "    }</xmp></code></pre>",
        bibliography: [],
        verified: true,
        approvements:[
          {
            userId: 101,
            name: "Theresa Sauer",
            image: "data:image/png;base64,"
          },
          {
            userId: 102,
            name: "Maike Schütze",
            image: "data:image/png;base64,"
          },
          {
            userId: 103,
            name: "Thomas Müller",
            image: "data:image/png;base64,"
          },
          {
            userId: 104,
            name: "Kirsten Meier",
            image: "data:image/png;base64,"
          },
          {
            userId: 105,
            name: "Dana Schmitz",
            image: "data:image/png;base64,"
          }
        ]
      }
    } else if (this.$route.query.g == 4) {
      this.guideline = {
        title: "Guideline mit Bild und Quellenangabe",
        name: "Pam Beesly",
        author_id: 3,
        last_update: "12.04.2023",
        text: "Es gibt viele schöne Bilder auf dieser Welt, und diese sollen natürlich auch im System einbindbar sein. Dafür kann es verschiedenste Gründe geben, aber das Sprichwort <i>Ein Bild sagt mehr als tausend Worte</i> <b>[1]</b> wird nicht lügen, daher sollten Bilder definitiv problemlos in einer Guideline einbindbar sein. Für dieses Beispiel gehe ich davon aus, dass das Bild als Weblink existiert und nicht als Base64, aber das kann problemlos geändert werden, sollten wir hier eine andere Architekturentscheidung treffen.\n\n<img src='https://www.th-koeln.de/mam/bilder/hochschule/organisation/standorte/fittosize_705_0_234e2e5854dd50684360c17e33d3353f_th_koln_campus_gummersbach_sebastian_hopp.jpg'><b>[2]</b>\n\nDie TH Köln (Technische Hochschule Köln) ist eine renommierte deutsche Hochschule mit einem breiten Spektrum an Studiengängen in den Bereichen Technik, Wirtschaft, Sozialwissenschaften und Kultur. Mit modernen Einrichtungen, engagierten Dozenten und einer praxisorientierten Ausbildung bietet die TH Köln ihren Studierenden eine erstklassige akademische Erfahrung. Durch enge Verbindungen zur Industrie und zahlreiche Forschungsprojekte ermöglicht die TH Köln ihren Absolventen einen nahtlosen Übergang in die Arbeitswelt. Entdecke die vielfältigen Möglichkeiten, die die TH Köln bietet, um deine beruflichen Ziele zu erreichen und deine Leidenschaft in die Tat umzusetzen.",
        bibliography: [
          {
            id: 1,
            text: "'Ein Bild sagt mehr als tausend Worte' Wikipedia-Artikel, abgerufen am 11.07.2023. https://de.wikipedia.org/wiki/Ein_Bild_sagt_mehr_als_tausend_Worte"
          },
          {
            id: 2,
            text: "Bild von der Website der TH Köln, abgerufen am 11.07.2023. https://www.th-koeln.de/hochschule/campus-gummersbach_76496.php"
          }
        ],
        verified: true,
        approvements: [
          {
            userId: 105,
            name: "Dana Schmitz",
            image: "data:image/png;base64,"
          }
        ]
      }
    } else {
      this.getGuideline()
    }
  },
  mounted() {
    document.addEventListener("selectionchange", debounce(this.getSelectionText,500));
  },
  methods: {
    async getGuideline(){
      let result = await AuthService.getGuideline({"guideline_id": this.$route.query.g})
      if(result.msg){
        result.msg.last_update = moment(result.msg.last_update).format("DD.MM.Y, HH:mm")
        this.guideline = result.msg
        this.guideline.approvements.forEach(approvement => {
          if(approvement.id == this.$store.getters.getUser.id){
            this.alreadyApproved = true
          }
        })
      }
    },
    async getAnnotation(input){
      document.removeEventListener("click", this.handleClick)
      document.removeEventListener("scroll", this.handleClick)
      if(this.activeAnnotation && this.activeAnnotation.id === parseInt(input.srcElement.id)){
        document.getElementById("annotation").classList.remove("active")
        this.activeAnnotation = null;
      } else {
        let data = {
          "annotation_id": input.srcElement.id
        }
        if(this.$store.getters.isLoggedIn) data.user_id = this.$store.getters.getUser.id

        let result = await AuthService.getAnnotation(data)
        this.activeAnnotation = result.msg

        /*this.activeAnnotation = {
          id: 999,
          name: "Thomas Schmitz",
          image: "data:image/png;base64,",
          score: 12,
          userVote: "d",
          text: "Diese Textstelle finde ich verbesserungswürdig, weil lorem ipsum dolor ist und so weiter und sofort!"
        }*/
        document.getElementById("annotation").classList.add("active")
        document.getElementById("annotation").style.top = (input.srcElement.getBoundingClientRect().y + 30).toString() + "px";
        document.getElementById("annotation").style.left = ((input.srcElement.getBoundingClientRect().x / 2)).toString() + "px";
        document.addEventListener("click", this.handleClick)
        document.addEventListener("scroll", this.handleClick)
        this.timeOfAnnotationOpen = moment();
        this.handleVoteButtonColor()
      }
    },
    buildAnnotationLinks(){
      let annoLinks = document.getElementsByClassName("annotationLink")
      for(let i in annoLinks){
        if (typeof(annoLinks[i]) === "object"){
          annoLinks[i].addEventListener("click", this.getAnnotation);
        }
      }
    },
    async handleAnnotationVote(input){
      let upvote = input.srcElement.id === "upvote" ? 1 : 0

      let data = {
        "annotation_id": this.activeAnnotation.annotation_id,
        "user_id": this.$store.getters.getUser.id,
        "upvote": upvote
      }

      await AuthService.voteAnnotation(data)

      data = {
      "annotation_id": this.activeAnnotation.annotation_id
      }

      if(this.$store.getters.isLoggedIn) data.user_id = this.$store.getters.getUser.id

      let result = await AuthService.getAnnotation(data)
      this.activeAnnotation = result.msg

      this.handleVoteButtonColor()
    },
    handleVoteButtonColor(){
      if(document.getElementById("upvote") && document.getElementById("downvote")){
        if(this.activeAnnotation.userVote === "u"){
          document.getElementById("upvote").classList.add("active")
          document.getElementById("downvote").classList.remove("active")
        } else if (this.activeAnnotation.userVote === "d"){
          document.getElementById("upvote").classList.remove("active")
          document.getElementById("downvote").classList.add("active")
        } else {
          document.getElementById("upvote").classList.remove("active")
          document.getElementById("downvote").classList.remove("active")
        }
      }
    },
    handleClick(evt){
      if(this.activeAnnotation != null && document.getElementById("annotation").classList.contains("active") && this.timeOfAnnotationOpen !== null && moment().diff(this.timeOfAnnotationOpen, 'seconds', true) > 0.1){
        const annotationEL = document.getElementById("annotation");
        let clickedEL = evt.target;
        do {
          if(clickedEL === annotationEL) {
            // do nothing
            return;
          }
          // Go up the DOM
          clickedEL = clickedEL.parentNode;
        } while (clickedEL);
        // This is a click outside.
        document.getElementById("annotation").classList.remove("active")
        this.activeAnnotation = null;
      }
    },
    async approveGuideline(){
      let data = {
        "expert_id" : this.$store.getters.getUser.id,
        "guideline_id": this.$route.query.g
      }

      let result = await AuthService.approveGuideline(data)

      if(result.msg == "Success"){
        this.$router.go()
      }
    },
    async revertApproval(){
      let data = {
        "expert_id" : this.$store.getters.getUser.id,
        "guideline_id": this.$route.query.g
      }

      let result = await AuthService.revertApproval(data)

      if(result.msg == "Success"){
        this.$router.go()
      }
    },
    async saveAnnotation(){
      let data = {
        "guideline_id" : this.$route.query.g,
        "author_id": this.$store.getters.getUser.id,
        "text": this.annotationText,
        "selected_text": this.selectedText
      }

      let result = await AuthService.saveAnnotation(data)
      if(result.msg == "Success"){
        this.$router.go()
      }
    },
    getSelectionText() {
      if(!this.selectedText){
        let text = "";
        if (window.getSelection) {
          text = window.getSelection().toString();
        } else if (document.selection && document.selection.type != "Control") {
          text = document.selection.createRange().text;
        }
        this.selectedText = text
      }
    }
  },
  updated(){
    hljs.highlightAll()
    this.buildAnnotationLinks()
  }
}
</script>
