<template>
  <div class="guidelines">
    <NavHeader :links=this.navLinks />
    <div class="content has-gap">
      <div class="navbar">
        <select name="category" id="category" @change="getGuidelines">
          <option value="" selected>Alle Kategorien</option>
          <!--<option value="sight">Sehbehinderungen</option>-->
          <!--<option value="audio">Hörbehinderungen</option>-->
          <!--<option value="movement">Mobilitätseinschränkungen</option>-->
          <option value="examples">Beispielguidelines</option>
        </select>
        <!--<input type="text" name="search" id="search" placeholder="Suche">-->
      </div>
      <div class="loading" v-if="!guidelines || guidelines.length == 0"></div>
      <div class="guideline_list" v-if="guidelines && guidelines.length > 0">
        <router-link :to="'/guideline?g=' + guideline.guideline_id" class="guideline" v-for="guideline in guidelines" :key="guideline.id" :value="guideline.id">
          <div class="top_row">
            <h2>{{guideline.title}}</h2>
            <div class="approvements">
              <div class="approvement_list" v-if="guideline.approvements">
                <router-link v-for="approvement in guideline.approvements.slice(0, 5)" :to="'/user?u=' + approvement.id" :key="approvement.userId" :value="approvement.userId"><img :src=approvement.image><p class="expert_name">{{approvement.name}}</p></router-link>
                <img v-if="guideline.approvements && guideline.approvements.length >= 5" src="@/assets/images/verified.png" class="verified_badge" title="Diese Guideline ist verifiziert">
                <img v-if="!guideline.approvements || guideline.approvements.length < 1" src="@/assets/images/warning.png" class="no_approvements" title="Diese Guideline wurde noch nicht bestätigt">
              </div>
              <h3 v-if="guideline.approvements && guideline.approvements.length > 0">durch <b>{{guideline.approvements.length}}</b> Experten bestätigt</h3>
              <h3 v-if="guideline.approvements && guideline.approvements.length < 1">noch nicht bestätigt</h3>
            </div>
          </div>
          <p>{{truncate(stripHTML(guideline.text))}}</p>
        </router-link>
      </div>
  </div>
  </div>
</template>

<script>

import NavHeader from "@/components/navHeader.vue";
import { set, get } from 'idb-keyval'
import AuthService from "@/services/AuthService";

export default {
  name: 'HomeView',
  components: {NavHeader},
  data(){
    return {
      navLinks: [
        {
          link: "/guidelines",
          name: "guidelines"
        }
      ],
      guidelines: [],
      guidelineExamples: [
        {
          guideline_id: 1,
          title: "Optimieren des DOM für Screenreader",
          teaser: "Da viele Menschen mit visuellen Einschränkungen zur Interaktion mit Webseiten sogenannte Screenreader verwenden, ist es von großer Relevanz, das Document Object Model einer Webseite mit diesem Fakt im Hinterkopf zu gestalten. Diese Guideline erläutert, auf welche Gegebenheiten dabei geachtet werden sollte, um eine Webseite für die Nutzung anhand eines Screenreaders zu optimieren.",
          approvements:[
            {
              userId: 101,
              name: "Theresa Sauer",
              image: "data:image/png;base64,"
            },
            {
              userId: 102,
              name: "Maike Schütze",
              image: "data:image/png;base64,"
            },
            {
              userId: 103,
              name: "Thomas Müller",
              image: "data:image/png;base64,"
            },
            {
              userId: 104,
              name: "Kirsten Meier",
              image: "data:image/png;base64,"
            },
            {
              userId: 105,
              name: "Dana Schmitz",
              image: "data:image/png;base64,"
            }
          ]
        },
        {
          guideline_id: 2,
          title: "Störfaktoren vermeiden",
          teaser: "Die Fähigkeit der Nutzer, Inhalte zu lesen und zu bedienen, darf keinesfalls beeinträchtigt werden, wenn die Seite barrierefrei sein soll. In der Praxis werden Seiten oft \"im Wesentlichen barrierefrei\" oder \"barrierearm\" umgesetzt, aber manchmal sind genau die Aspekte, die nicht barrierefrei umgesetzt werden, so problematisch, dass der gesamte Webauftritt nicht genutzt werden kann.",
          approvements:[
            {
              userId: 102,
              name: "Maike Schütze",
              image: "data:image/png;base64,"
            },
            {
              userId: 103,
              name: "Thomas Müller",
              image: "data:image/png;base64,"
            }
          ]
        },
        {
          guideline_id: 3,
          title: "Beispielguideline mit Code-Einbindung",
          teaser: "Nutzer müssen auf der Grundlage der Web Content Accessibility Guidelines (WCAG) 2.0 die Möglichkeit haben, blinkende Elemente in einen statischen Zustand versetzen zu können. Blinken bedeutet, dass ein Inhalt zwischen zwei oder mehr visuellen Zuständen wechselt und kann sehr viele Situationen betreffen. In diesem Beitrag wird ein kleiner JavaScript geboten, um blinkenden Text standardkonform anzuzeigen.",
          approvements:[
            {
              userId: 101,
              name: "Theresa Sauer",
              image: "data:image/png;base64,"
            },
            {
              userId: 102,
              name: "Maike Schütze",
              image: "data:image/png;base64,"
            },
            {
              userId: 103,
              name: "Thomas Müller",
              image: "data:image/png;base64,"
            }
          ]
        },
        {
          guideline_id: 4,
          title: "Beispielguideline mit Bild und Quellenangabe",
          teaser: "Im Gegensatz zu Druckmedien, die in der Regel aus Inhalt und einem vorangestellten Inhaltsverzeichnis bestehen, wird auf Webseiten die Navigation gleich mit dem Inhalt geliefert. Fast alle Webseiten bestehen aus Inhalt und mindestens einem Navigationsbereich mit Links zu anderen Seiten des gleichen Anbieters. Je nach Webauftritt können die Navigationsbereiche sehr umfangreich werden.",
          approvements : [
            {
              userId: 105,
              name: "Dana Schmitz",
              image: "data:image/png;base64,"
            }
          ]
        },
        {
          guideline_id: 5,
          title: "Reingelegt — Per Tastatur in die Falle",
          teaser: "Die Tastaturbedienung gehört zu den entscheidenden Kriterien des barrierefreien Webdesigns. Die Vermeidung von Tastaturfallen wird dabei in den Web Content Accessibility Guidelines (WCAG) 2.0 eine besondere Gewichtung gegeben. Die Vermeidung von Tastaturfallen ist eine von vier Erfolgskriterien, die für eine Konformität immer erfüllt sein müssen.",
          approvements: [

          ]
        },
        {
          guideline_id: 6,
          title: "Kontextänderungen und die Tastaturbedienung",
          teaser: "Es gibt viele Aspekte der Tastaturbedienung, etwa die vollständige Funktionalität bei Links, Formularen und Widgets, die Fokus-Reihenfolge oder die Sichtbarkeit des Fokus. Natürlich geht es zum einen darum, dass jede Funktion mit der Tastatur aktiviert bzw. ausgewählt werden kann, aber es geht zum anderen auch darum, dass bei der Bedienung einer Webseite mit der Tastatur keine unvorhersehbaren Aktionen ausgelöst werden.",
          approvements: [

          ]
        }
      ]
    }
  },
  async mounted(){
    await this.getGuidelines(await get("selectedCategory"));
  },
  methods : {
    async getGuidelines(category = null){
      let selected = null
      if(category && category.target){
        selected = category.target.options[category.target.options.selectedIndex].value
      } else {
        selected = category
      }
      await set("selectedCategory", selected)
      this.setSelectField(selected);

      if(selected && selected === "examples"){
        this.guidelines = this.guidelineExamples
      } else { //get guidelines from api
        let result = await AuthService.getGuidelines({"category": selected})
        this.guidelines = result.msg
      }
    },
    setSelectField(selected) {
      let select = document.getElementById("category");
      if(select){
        for(let i in select.options) {
          if (select.options[i].value === selected) {
            select.options[i].selected = true;
          }
        }
      }
    },
    stripHTML(html){
      let tmp = document.createElement("div")
      tmp.innerHTML = html
      return tmp.textContent||tmp.innerText
    },
    truncate(string){
      let teaserLength = 250
      if(string.length <= teaserLength) return string
      return string.slice(0, teaserLength) + "..."
    }
  }
}
</script>
